@import ./_global
@import ./_vendors
@import ./_colors
@import ./_text
@import ./_variables

html, body
  font-family: $font-family, sans-serif
  font-weight: 200
  line-height: 1.2
  -webkit-font-smoothing: antialiased
  -moz-osx-font-smoothing: grayscale
  user-select: none
  cursor: default

html
  letter-spacing: normal
  -webkit-text-size-adjust: 100%

body
  padding: 0
  margin: 0

#root
  position: relative
  width: 100%
  min-height: 100vh
  display: flex
  flex-direction: column
  background: #FEFEFA
  box-sizing: border-box

.main
  margin: 0 auto
  width: MIN(900px, 96vw)
  min-height: 100%
  flex-grow: 1
